<template>
  <!-- info -->
  <div class="border-bottom border-dark px-4">
    <h5>{{ productData.title }}</h5>
    <h6>{{ productData.describe }}</h6>
  </div>
  <!-- img -->
  <div
    class="border-top border-dark p-4 d-flex justify-content-center align-items-center h-100 position-relative"
  >
    <div class="position-absolute top-0 end-0">
      <nav aria-label="Page navigation example">
        <ul class="pagination">
          <li class="page-item disabled">
            <a
              class="page-link border-dark border-top-0 border-2 rounded-0"
              href="#"
              aria-label="Previous"
            >
              <span aria-hidden="true">&laquo;</span>
            </a>
          </li>
          <li class="page-item">
            <a
              class="page-link border-dark border-top-0 border-2 rounded-0 active"
              href="#"
              >1</a
            >
          </li>
          <li class="page-item">
            <a
              class="page-link border-dark border-top-0 border-2 rounded-0"
              href="#"
              >2</a
            >
          </li>
          <li class="page-item">
            <a
