          <li class="page-item">
            <a
              class="page-link border-dark border-top-0 border-end-0 border-2 rounded-0"
              href="#"
              aria-label="Next"
            >
              <span aria-hidden="true">&raquo;</span>
            </a>
          </li>
        </ul>
      </nav>
    </div>
    <img :src="productData.img" style="width: 75%" alt="" />
  </div>
