<script>
import cakeImg1 from '@/assets/images/cake-01.png';
import cakeImg2 from '@/assets/images/cake-02.png';
import cakeImg3 from '@/assets/images/cake-03.png';
import sandwich from '@/assets/images/sandwich.png';
import drinkImg from '@/assets/images/drink.png';

export default {
  data() {
    return {
      productData: {},
      testData: [
        { img: sandwich, id: '000' },
        { img: cakeImg1, id: '001' },
        { img: cakeImg2, id: '002' },
        { img: cakeImg3, id: '003' },
        { img: drinkImg, id: '004' },
        { img: drinkImg, id: '005' },
      ],
    };
  },
  mounted() {
    this.$emitter.on('productData', (e) => {
      this.productData = e;
      this.testData.forEach((item) => {
        if (item.id === this.productData.id) {
          this.productData.img = item.img;
        }
      });
    });
  },
};
</script>
